@import url('https://fonts.googleapis.com/css2?family=Calligraffitti&family=Dosis:wght@500&family=Josefin+Slab:wght@100&family=Merienda&family=Montserrat:wght@100;500&family=Nova+Round&family=Style+Script&display=swap');

$font-1:'Nova Round', serif;
$font-2:'Dosis',cursive;
$font-3:'Josefin Sans',sans serif;

$color-1: rgba(170, 170, 170, 0.582);
$color-2: rgba(34, 34, 238, 0.698);
 
$border-radius: 5px solid black;


*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    overflow: hidden;
}
body{
     min-height: 60vh;
     height: 100vh;
     font-family: $font-3;
 }

 li{
     list-style-type: none;
     
 }

 a{
     text-decoration: none;
 }
button{
    cursor: pointer;
    border-radius: 5px;
    height: 40px;
    width:100px;
    color: white;
    background: rgba(22, 4, 122, 0.938);
    font-size: 1rem;
}



