.main{
    .main-text{
        width: 100%;
        height: 70vh;
        padding-left: 85px;

        h1{
            font-family: $font-3;
            text-transform: capitalize;
            font-size: 4rem;
            font-weight: 600;
            letter-spacing: .15rem;
            margin-top: 2rem;

        }
        p{
            font-family: $font-1;
            font-size: 1.3rem;
            margin: 20px 0;
        }
        button{
            border: none;

           &:hover{
               background:white;
               color:rgba(42, 5, 253, 0.938);
               font-weight: bold;
               border: 1px solid rgba(0, 0, 255, 0.808);
           }
        }
    }
}