.courses_main{
    
        width: 100vw;
        height: 70vh;
        padding-left: 85px;
        &:before{
            content: '';
            position: absolute;
            bottom:  20%;
            left: -20px;
            width: 80px;
            height: 80px;
            border-radius: 50%;      
            background: #ffa291c9;
            z-index: 100;
        }
        &:after{
            content: '';
            position: absolute;
            bottom:  45%;
            left: 25%;
            width: 85px;
            height: 85px;
            border-radius: 50%;      
            background: #ffa291c9;
            opacity: .6;
            z-index: 100;
        }

        h2{
            font-family: $font-2;
            text-transform: capitalize;
            font-size: 2rem;
            font-weight: 600;
            letter-spacing: .15rem;
            margin-top: 2rem;

        }
        p{
            font-family: $font-1;
            font-size: 1.3rem;
            margin: 20px 0;
            width:40%;
            
        } 
}