.whyUs{
    font-family:$font-2 ;
    height: 100vh;
    width:100vw ;
   
    position:relative;

    
    
    &:before{
        content: '';
        position: absolute;
        bottom:  20%;
        left: -20px;
        width: 80px;
        height: 80px;
        border-radius: 50%;      
        background: #ffa291c9;
        z-index: 100;
    }
    
    
    .main-pic{
        position: absolute;
        z-index: -1;
        height: 85%;
        width: 60%;
        right: 0;
        
      
    }

}
