.header{
    
   nav{
       display: flex;
       justify-content: space-between;
       align-items: center;
       padding: 0 20px 20px 20px;
       height: 15vh;
       width: 100%;

      .logo{
          border-bottom: 5px solid rgb(204, 204, 3);
          margin-left: 80px;
         
    } 
      ul{
          display: inline-flex;
         
          li{
              display:flex ;
              justify-content: space-between;
              user-select: none;
              padding-left: 5px;
              border-left: 3px solid rgb(204, 204, 3);
              margin-left: 5px;
              width:120px;
              height: 25px;

             

              .btn{
                height: 25px;

                 &:hover{
                   color: rgba(22, 4, 122, 0.938);
                   background: white;
                   
                 } 

              }
           }
           .courses, .why, .support{
            width: 100px;
            transition: all .3s ease-in-out;
            &:hover{
                background: $color-2;

            }
        }
             



        }




    }
   





}